import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../../context/authContext";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import "./Register.css";
import NavBtnBack from "../utils/NavBtnBack";
import { Link } from "react-router-dom";

//definimos un estado
export function Register() {
  const [user, setUser] = useState({
    email: "",
    password: "",
  });
  const { signUp } = useAuth();
  //ejecuto useAuth y me devuelve el objeto signup que tiene la funcion
  const navigate = useNavigate();
  //es un hook que nos devuelve una función navigate que me va a redirigir
  const [error, setError] = useState();


//creamos una función handleChange  y recibe un objeto de evento
  //desde el evento se que existe una propiedad target, y de él extraigo el name y value
  const handleChange = ({ target: { name, value } }) =>
    setUser({ ...user, [name]: value });
//establecemos en user el valor de name y el valor de name viene desde value, es un objeto porque user es un objeto
//primero va a copiar todos los datos que tenga user  (...user y luego actualizas)


//toda petición es asincróna, va a tomar tiempo
const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    //con esto al ingresar correctamente se va el error
    try {
      await signUp(user.email, user.password);
      navigate("/show");
      //si logra registrarse correctamente navega a show
    //sino, que me muestre el error
    } catch (error) {
      setError(error.message);
    }
    //en el momento apriete submit ejecuto signup y le paso 2 cosas, user.email y user.pass

  };
//si existe un error ejecutamos una etiqueta que va a mostrar el error
  return (
    <>
    <div className="containerBtnBack">
      <NavBtnBack path="/" />
      </div>

      {error && <p>{error}</p>}
    
      <Form className="form" onSubmit={handleSubmit}>
        <div className="section">
          <h2>Crea tu cuenta</h2>
          <div className="containerInput">
            <label className="labRegister" htmlFor="email">
              Registre su Correo
            </label>

            <Form.Control
              className="formInputs"
              type="email"
              name="email"
              placeholder="youremail@mail.com"
              onChange={handleChange}
            />

            <label className="labRegister" htmlFor="password">
              Crea tu contraseña
            </label>
            <Form.Control
              className="formInputs"
              type="password"
              name="password"
              placeholder="******"
              onChange={handleChange}
            />
          </div>
          <Button variant="outline-info" id="btnLogin" type="submit">
            Crear cuenta
          </Button>

          <label htmlFor="email">¿Ya tienes una cuenta?</label>
          <Link to="/login">
            <Button variant="outline-info" id="btnLogin" type="submit">
              Ingresa sesión
            </Button>
          </Link>
        </div>
      </Form>
    </>
  );
}
export default Register;
